}
.Boddy{
  position: relative;
  animation-name: example;
  animation-duration: 4s;
  animation-delay: 3s;
  animation-iteration-count: 3;
}
@keyframes example {
  0%   {transform:rotateY(180deg);}
  25%  {transform:rotateY(144deg);}
  50%  {transform:rotateY(108deg);}
  75%  {transform:rotateY(72deg);}
  100% {transform:rotateY(36deg);}
  125% {transform:rotateY(0deg);}

}